a.white-btn {
  position: relative;
  font-size: 14px;
  padding: 12px 16px;
  background: transparent;
  box-sizing: border-box;
  text-align: center;
  border-radius: 10px;
  border: 1.3px solid var(--white);
  transition: 0.2s box-shadow, 0.3s transform;
}
a.white-btn:active {
  transform: scale(0.96);
}
a.white-btn:hover {
  box-shadow: 0px 0px 8px 0px rgb(255, 255, 255);
}

a.btn {
  position: relative;
  font-size: 14px;
  text-align: center;
  padding: 12px 16px;
  border-radius: 10px;
  background: var(--blueGradient);
  box-sizing: border-box;
  border: 1.3px solid var(--blue);
  transition: 0.2s box-shadow, 0.3s transform;
}
a.btn:active {
  transform: scale(0.96);
}
