  width: 42px;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: center;
}
a.btn.check-btn > img {
  width: 24px;
  height: 24px;
}
a.btn.disabled {
  opacity: 0.5;
  pointer-events: none;
}
a.btn:hover {
  box-shadow: 0px 0px 10px 0px rgb(28, 58, 195);
}

